import { useState, useEffect } from "react";
// import { createClient } from 'contentful/dist/contentful.browser.js';
import { createClient } from "contentful";

// Data from Contentful
const SPACE_ID = "dqksinko7d89";
const ACCESS_TOKEN = "BO_5OOjnEJRtI163ojOFAZwnMWPPlMtQ17mUdi9AKRc";

const client = createClient({
  space: SPACE_ID,
  accessToken: ACCESS_TOKEN,
});

const useContentfulData = (contentType, region) => {
  const [data, setData] = useState({});
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    client.getEntries({ content_type: contentType }).then(function (entries) {
      setData(entries);
      setLoading(false);
    });
  }, []);
  return [data, loading];
};

export { useContentfulData };
